.App-header .pf-c-title {
  --pf-c-title--m-4xl--FontSize: 3rem;
}

$danger-color: #f7923d;
.decision {
  line-height: 10px;
  &__outcome {
    display: block;
    background-color: $danger-color;
    color: white;
    border-radius: 4px;
    padding: 4px 6px;
    font-weight: bold;
    font-size: (20/16)+rem;
    text-transform: uppercase;
  }
  &__score {
    display: block;
    position: relative;
    top: -8px;
    font-size: (20/16)+rem;
    text-transform: uppercase;
    &__label {
      margin-right: 4px;
    }
    &__value {
      color: $danger-color;
      font-weight: bold;
    }
    &__bar {
      display: block;
      height: 10px;
      width: 100%;
      position: absolute;
      margin-top: 4px;
      background: linear-gradient(90deg, rgba(251,6,2,1) 0%, rgba(255,209,0,1) 100%);
      &__indicator {
        width: 2px;
        height: 100%;
        position: absolute;
        background: grey;
      }
    }
  }
}

.decision-outcome-badge {
  border-radius: 4px;
  padding: 0.4rem 0.4rem 0.2rem;
  font-weight: bold;
  font-size: 1rem;
  text-transform: uppercase;
  text-align: center;
  width: 120px;
  display: inline-block;
  &--approved {
    color: var(--pf-global--success-color--200);
    background-color: var(--pf-global--success-color--100
    );
  }
  &--denied {
    //background-color: var(--pf-global--danger-color--100);
    color: var(--pf-global--danger-color--100);
  }
}

.editor {
  .pf-c-modal-box__body {
    overflow: hidden;
  }
  &__wrap {
    position: relative;
  }
  &__switch {
    position: absolute;
    top: 0;
    height: 60px;
    width: 100%;
    background-color: #fff;
    z-index: 100;
  }
  &__iframe {
    iframe {
      width: 100%;
      height: 80vh;
    }
  }  
}

.input-list {
  &__headings {
    font-weight: bold;
  }
  &__has-chart {
    .pf-c-data-list__item-content {
      padding-bottom: 0;
    }
  }
  &__chart {
    --pf-c-data-list__cell-cell--PaddingTop: 0;
  }
}